.navbar{
    position: fixed;
    top:0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: #111;
    z-index: 999;

    transition-timing-function: ease-in;
    transition:all 0.5s
}
.logo{
    position: fixed;
    left :20px;
    width :80px
}
.avatar{
    position: fixed;
    right: 20px;
    width: 35px;
    height: 35px;
    margin-bottom: 10px;

}